<template>
  <div class="pay">
    <van-nav-bar title="提交订单" left-text="返回" left-arrow @click-left="returnLast" />

    <div class="address-box">
      <div class="address">
        <div class="clearfix address-select" @click="openAddressBox">
          <div class="fl address-title">选择收货地址</div>
          <div class="fl arrow-box">
            <van-icon name="arrow" class="arrow-icon" />
          </div>
        </div>
        <div class="address-content">
          <div class="clearfix">
            <div class="fl name">{{currentAddress.name}}</div>
            <div class="fl phone">{{currentAddress.tel}}</div>
          </div>
          <div class="address-info">{{currentAddress.address}}</div>
        </div>
      </div>
    </div>

    <div class="order">
      <div class="order-content">
        <van-swipe-cell>
          <div class="clearfix" v-for="(item,index) in shopbagData" :key="index">
            <div class="order-img fl">
              <img class="auto-img" :src="item.small_img" alt />
            </div>
            <div class="order-info fl">
              <div class="clearfix">
                <div class="text-box fl">
                  <div class="text-name">{{item.name}}</div>
                  <div class="enname">{{item.enname}}</div>
                  <div class="text-rule">{{item.rule}}</div>
                </div>
                <div class="price-box fr">
                  <div class="price">￥{{item.price}}</div>
                  <div class="count">x {{item.count}}</div>
                </div>
              </div>
            </div>
          </div>
        </van-swipe-cell>
         <div class="pro-box">
          <div class="clearfix pro-boxing">
            <div class="fl pro-count">共计 {{product.count}} 件商品</div>
            <div class="fr pro-total">合计 ￥{{product.total}}</div>
          </div>
          <div class="left-box left"></div>
          <div class="left-box right"></div>
        </div>
      </div>
    </div>
    


    <div class="pay-btn">
      <van-button round block color="#0C34BA" @click="pay">立即结算</van-button>
    </div>

    <van-popup v-model="isOpen" position="bottom">
      <div class="address-title-box">选择收货地址</div>
      <van-address-list
        v-model="chosenAddressId"
        :list="list"
        default-tag-text="默认"
        @select="selectAddress"
        @add="newAddress"
      />
    </van-popup>
  </div>
</template>

<script>
import {utils} from '../assets/js/utils'
export default {
  data() {
    return {
      isOpen: false,
      chosenAddressId: '',
      //地址列表数据
      list: [],

      sids:[],

      product:{
        count:0,
        total:0
      },
      shopbagData:[],

      currentAddress:{
          name: '',
          tel: '',
          address: ''
      }
        

    };
  },
  created(){
    this.sids = this.$route.query.sids.split('-');
    
    //获取订单商品数据
    this.getOrderShop();

    //查询收货地址
    this.getReceiveAddress();
  },
  methods: {

    openAddressBox() {
      this.isOpen = true;
    },
    
    //选择地址
    selectAddress(item,index) {
      this.isOpen = false;
      for (let key in this.currentAddress) {
        this.currentAddress[key] = item[key];
      }
    },
    //查询收货地址
    getReceiveAddress(){
      let tokenString = localStorage.getItem('CSTK');
      if (!tokenString) {
        return;
      }
      this.$toast.loading({
          message: '加载中...',
          forbidClick: true,
          duration: 0,
          loadingType: 'spinner'
        })

      this.axios({
        method:'GET',
        url:'/findAddress',
        params:{
          appkey:this.appkey,
          tokenString
        }
      }).then(result =>{
        this.$toast.clear();
        
        if (result.data.code == 20000) {
          
          result.data.result.map(v =>{
            let address = v.province + v.city + v.county + v.addressDetail;
            this.list.push({
              id: v.aid,
              name: v.name,
              tel: v.tel,
              address,
              isDefault:Boolean(v.isDefault)
            })

            if (Boolean(v.isDefault)) {
              this.chosenAddressId = v.aid;
              this.currentAddress.name = v.name;
              this.currentAddress.tel = v.tel;
              this.currentAddress.address = address;
            }
          })
        }
      }).catch(err =>{
        this.$toast.clear();
        
      })
    },

    //新增地址
    newAddress() {
      this.$router.push({ name: "NewAddress" });
    },

    returnLast() {
      this.$router.go(-1);
    },

    //获取订单商品数据
    getOrderShop() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner"
      });

      this.axios({
        method: 'GET',
        url: 'commitShopcart',
        params:{
          appkey:this.appkey,
          tokenString,
          sids:JSON.stringify(this.sids)
        }
      }).then(result =>{
        this.$toast.clear();
        
        if (result.data.code == 50000) {
          result.data.result.map( v=>{
            this.product.count += v.count;
            this.product.total += v.count * v.price;
          })

          this.shopbagData = result.data.result;
        }
      }).catch(err =>{
        this.$toast.clear();
        
      })
    },

    pay(){
      if (this.chosenAddressId == '') {
        this.$toast('请选择收货地址');
        return;
      }

      let tokenString = localStorage.getItem('CSTK');
      if (!tokenString) {
        return;
      }

      let data = utils.queryString({
        appkey:this.appkey,
        tokenString,
        sids:JSON.stringify(this.sids),
        phone:this.currentAddress.tel,
        address:this.currentAddress.address,
        receiver:this.currentAddress.name
      })

      this.$toast.loading({
          message: '加载中...',
          forbidClick: true,
          duration: 0,
          loadingType: 'spinner'
        })

      this.axios({
        method: 'POST',
        url: '/pay',
        data
      }).then(result =>{
        this.$toast.clear();
        
        if (result.data.code == 60000) {
          this.$router.push({name:'Order'});
        }
      }).catch(err =>{
        this.$toast.clear();
        
      })
    }
  }
};
</script>

<style lang="less" scoped>
.pay {
  height: 100%;
  position: relative;
}
.address-box {
  padding: 10px;
  .address {
    .address-select {
      background-color: #f5f5f5;
      padding: 10px;
      border-radius: 15px;
      .address-title {
        font-size: 18px;
        font-weight: bold;
      }
      .arrow-box {
        font-size: 16px;
        margin-top: 6px;
        margin-left: 5px;
      }
    }
    .address-content {
      background-color: #f5f5f5;
      margin-top: 10px;
      padding: 10px;
      text-align: left;
      border-radius: 15px;
      .name {
        font-size: 14px;
        line-height: 14px;
      }
      .phone {
        font-size: 14px;
        line-height: 14px;
        margin-left: 10px;
      }
      .address-info {
        margin-top: 10px;
        font-size: 14px;
      }
    }
  }
}

.order {
  padding: 10px;

  .order-content {
    padding: 10px;
    background-color: #f5f5f5;
    border-radius: 15px;
    .clearfix{
      margin-bottom: 10px;
    }
    .order-img {
      width: 100px;
      height: 100px;
    }
    .order-info {
      margin-top: 30px;
      margin-left:10px;
      width: calc(~"100% - 110px");
      text-align: left;
      .text-box {
        .text-name {
          font-size: 18px;
          font-weight: bold;
          color: #000;
        }
        .enname {
          font-size: 12px;
          color: #999;
        }
        .text-rule {
          font-size: 14px;
          color: #999;
        }
      }
      .price-box {
        .price {
          font-size: 20px;
          color: #0c34ba;
          margin-bottom: 12px;
        }
        .count {
          font-size: 16px;
          text-align: right;
        }
      }
    }
  }
}

.pro-box {
      margin: 0 10px;
      margin-top: 30px;
      position: relative;
      background-color: #f5f5f5;
      border-top: 1px dashed black;
      .pro-boxing.clearfix {
        padding: 20px 10px 10px 10px;
        margin: 0;
        .pro-count {
          font-size: 16px;
          color: #000;
          font-weight: bold;
        }
        .pro-total {
          font-size: 16px;
          font-weight: bold;
          color: #0c34ba;
        }
      }
      .left-box{
          width: 30px;
          height: 30px;
          background-color: #fff;
          border-radius: 50%;
          position: absolute;
          top: -15px;
      }
      .left{
            left:-35px;
          }
          .right{
            right: -35px;
          }
    }
.pay-btn {
  position: fixed;
  left: 0;
  bottom: 0;
  width: calc(~"100% - 20px");
  padding: 0 10px;
}
/deep/.van-nav-bar .van-icon {
  color: #0c34ba;
}
/deep/.van-nav-bar__text {
  color: #0c34ba;
}
/deep/.van-tag--danger {
  background-color: #0c34ba;
}
/deep/.van-address-item .van-radio__icon--checked .van-icon {
  background-color: #0c34ba;
  border-color: #0c34ba;
}
/deep/.van-button--danger {
  background-color: #0c34ba;
  border-color: #0c34ba;
}
/deep/.van-address-item__edit{
  display: none;
}
.address-title-box {
  font-size: 16px;
  padding: 10px;
}
</style>